import React, { Fragment } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Navbar from 'react-bootstrap/Navbar';
import { logout } from '../user/user.actions';
import { Nav, NavDropdown, Container } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
const Menubar = () => {
  const { authUser } = useSelector(state => state.user);
  const dispatch = useDispatch();
  const handleLogout = () => {
    dispatch(logout());
  };

  return (
    <div className='header'>
      <Container>
        <Navbar collapseOnSelect expand='lg'>
          <Navbar.Brand as={NavLink} to='/'>
            <img className='logo' src='/assets/images/logo.png' alt='' />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls='responsive-navbar-nav' />
          <Navbar.Collapse id='responsive-navbar-nav'>
            <Nav className='mr-auto'>
              <Nav.Link as={NavLink} to='/'>
                Trang ch
              </Nav.Link>
              <Nav.Link as={NavLink} to='/cam-nang-du-lich'>
                Cm nang du lch
              </Nav.Link>
              <Nav.Link as={NavLink} to='/lich-trinh'>
                Bn mun n?
              </Nav.Link>
              {Object.keys(authUser).length > 0 ? (
                <Fragment>
                  <NavDropdown title='Ti khon' id='collasible-nav-dropdown'>
                    <NavDropdown.Item as={NavLink} to='/tai-khoan'>
                      Ti khon ca ti
                    </NavDropdown.Item>

                    <NavDropdown.Item onClick={handleLogout}>
                      ng xut
                    </NavDropdown.Item>
                  </NavDropdown>
                  <Nav.Link as={NavLink} to='/viet-bai'>
                    <i className='fas fa-edit'></i>
                    Vit bi
                  </Nav.Link>
                </Fragment>
              ) : (
                <Fragment>
                  <Nav.Link as={NavLink} to='/dang-ky'>
                    ng k
                  </Nav.Link>

                  <Nav.Link as={NavLink} to='/dang-nhap'>
                    ng nhp
                  </Nav.Link>
                </Fragment>
              )}
            </Nav>
            <Nav>
              <div className='igi_box_search'>
                <form className='search-form'>
                  <input
                    type='text'
                    className='form-control'
                    placeholder='T kha tm kim...'
                  />
                  <input type='submit' value='' />
                </form>
              </div>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </Container>
      <div className='clearfix'></div>
    </div>
  );
};

export default Menubar;
