import React, { useState } from "react";
import { Modal, ModalHeader, ModalBody } from "reactstrap";

const ModalChargePass = (props) => {
  const { className } = props;

  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);

  return (
    <span>
      <a className="link-pass" onClick={toggle} href="#!">
        i mt khu
      </a>
      <Modal isOpen={modal} toggle={toggle} className={(className, "modal-lg")}>
        <ModalHeader toggle={toggle}>i mt khu</ModalHeader>
        <ModalBody>
          <form>
            <div className="body-form">
              <div className="form-group row">
                <label htmlFor="inputPass" className="col-sm-3 col-form-label">
                  Mt khu c*
                </label>
                <div className="col-sm-9">
                  <input
                    type="password"
                    className="form-control"
                    id="inputPass"
                    placeholder="Nhp mt khu c"
                  />
                </div>
              </div>
              <div className="form-group row">
                <label htmlFor="inputPass" className="col-sm-3 col-form-label">
                  Mt khu mi*
                </label>
                <div className="col-sm-9">
                  <input
                    type="password"
                    className="form-control"
                    id="inputPass"
                    placeholder="Nhp mt khu mi"
                  />
                </div>
              </div>
              <div className="form-group row">
                <label htmlFor="inputPass" className="col-sm-3 col-form-label">
                  Xc nhn li
                </label>
                <div className="col-sm-9">
                  <input
                    type="password"
                    className="form-control"
                    id="inputPass"
                    placeholder="Xc nhn li mt khu"
                  />
                </div>
              </div>
              <div className="ft-btn fr">
                <button
                  type="submit"
                  className="btn btn-outline-success btn-add-item"
                >
                  Lu
                </button>{" "}
                &emsp;
                <button
                  type="submit"
                  className="btn btn-outline-dark"
                  onClick={toggle}
                >
                  Hy b
                </button>
              </div>
            </div>
          </form>
        </ModalBody>
      </Modal>
    </span>
  );
};

export default ModalChargePass;
